import ddaLine from "./algorithm/dda.line";
import bresenhamCirle from "./algorithm/bresenham.circle";
import bresenhamLine from "./algorithm/bresenham.line";
import ddaCircle from "./algorithm/dda.circle";
import midCircle from "./algorithm/mid.circle";
import midLine from "./algorithm/mid.line";

export const logicController = (algoFor, algo, data) => {
  const { x1, y1, x2, y2, radius } = data;
  let pointData = [];
  // console.log("from logic controller", dda);
  if (algoFor === "line") {
    switch (algo) {
      case "dda":
        pointData.length = 0;
        pointData = ddaLine(x1, y1, x2, y2);
        return pointData;

      case "mid":
        pointData.length = 0;
        pointData = midLine(x1, y1, x2, y2);
        return pointData;

      case "bresenham":
        pointData.length = 0;
        // console.log("pointData", pointData);
        pointData = bresenhamLine(x1, y1, x2, y2);
        // console.log("pointData", pointData);
        return pointData;

      default:
        break;
    }
  }
  if (algoFor === "circle") {
    switch (algo) {
      case "dda":
        pointData.length = 0;
        pointData = ddaCircle(x1, y1, radius);
        return pointData;
      case "mid":
        pointData.length = 0;
        pointData = midCircle(x1, y1, radius);
        return pointData;
      case "bresenham":
        pointData.length = 0;

        pointData = bresenhamCirle(x1, y1, radius);

        return pointData;

      default:
        break;
    }
  }
};
