const ddaCircle = (xc, yc, r) => {
  const CoOrdinates = [];
  let xc1, xc2, yc1, yc2, eps, sx, sy;

  let val, i;
  CoOrdinates.push({ x: xc + r, y: yc });
  xc1 = r;

  yc1 = 0;

  sx = xc1;

  sy = yc1;

  i = 0;

  do {
    val = Math.pow(2, i);

    i++;
  } while (val < r);

  eps = 1 / Math.pow(2, i - 1);

  do {
    xc2 = xc1 + yc1 * eps;
    yc2 = yc1 - eps * xc2;

    CoOrdinates.push({ x: Math.round(xc + xc2), y: Math.round(yc - yc2) });

    xc1 = xc2;

    yc1 = yc2;
  } while (yc1 - sy < eps || sx - xc1 > eps);

  return CoOrdinates;
};
export default ddaCircle;
