// v2
const bresenhamCirle = (Xc, Yc, R) => {
  const Coordinates = [];
  let X, Y;
  X = 0;
  Y = R;
  let d = 3 - 2 * R;
  Coordinates.push({ x: R + Xc, y: Yc });
  Coordinates.push({ x: Xc - R, y: Yc });
  Coordinates.push({ x: Xc, y: Yc + R });
  Coordinates.push({ x: Xc, y: Yc - R });
  while (Y >= X) {
    if (d < 0) {
      d = d + 4 * X + 6;
      X = X + 1;
      Coordinates.push({ x: Y + Xc, y: X + Yc });
      Coordinates.push({ x: X + Xc, y: Y + Yc });
      Coordinates.push({ x: -X + Xc, y: Y + Yc });
      Coordinates.push({ x: -Y + Xc, y: X + Yc });
      Coordinates.push({ x: -Y + Xc, y: -X + Yc });
      Coordinates.push({ x: -X + Xc, y: -Y + Yc });
      Coordinates.push({ x: X + Xc, y: -Y + Yc });
      Coordinates.push({ x: Y + Xc, y: -X + Yc });
    } else {
      d = d + 4 * (X - Y) + 10;
      X = X + 1;
      Y = Y - 1;
      Coordinates.push({ x: Y + Xc, y: X + Yc });
      Coordinates.push({ x: X + Xc, y: Y + Yc });
      Coordinates.push({ x: -X + Xc, y: Y + Yc });
      Coordinates.push({ x: -Y + Xc, y: X + Yc });
      Coordinates.push({ x: -Y + Xc, y: -X + Yc });
      Coordinates.push({ x: -X + Xc, y: -Y + Yc });
      Coordinates.push({ x: X + Xc, y: -Y + Yc });
      Coordinates.push({ x: Y + Xc, y: -X + Yc });
    }
  }
  return Coordinates;
};

export default bresenhamCirle;
