const midLine = (X1, Y1, X2, Y2) => {
  const CoOrdinate = [];
  // calculate dx & dy
  CoOrdinate.push({ x: X1, y: Y1 });
  CoOrdinate.push({ x: X2, y: Y2 });
  let dx = X2 - X1;
  let dy = Y2 - Y1;

  // initial value of decision parameter d
  let d = (dy - dx) * 2;
  let di = 2 * dy - dx;
  let x = X1;
  let y = Y1;
  while (x < X2) {
    if (di < 0) {
      x = x + 1;
      di = di + 2 * y;
      CoOrdinate.push({ x, y });
    } else {
      x = x + 1;
      y = y + 1;
      di = di + d;
      CoOrdinate.push({ x, y });
    }
  }

  return CoOrdinate;
};

export default midLine;
